export default class GlobalEventDistributor {
  stores = new Map();
  reloadEvents = new Map();

  registerStore = (name, store) => {
    this.stores.set(name, store);
    store.name = name;
    const events = this.reloadEvents.get(name);
    if (events && events.length > 0) {
      this.reloadEvents.delete(name);
      events.forEach(async event => await store.dispatch(event));
    }
  };

  getStore = app => this.stores.get(app);

  addReloadEvents(name, events) {
    this.reloadEvents.set(name, events);
  }

  unregisterStore = store => {
    this.stores.delete(store);
  };
  /**
   * this is similar to Rxjs BehaviorSubject, later registered stores are dispatched
   * with most recently dispatched event. E.g: Login action is dispatched by Navigation App before Activity App
   * and its store are loaded, yet Activity App's store also wants to listen to Login action to have auth information
   * stored in its own store.
   */
  dispatch = event => {
    this.stores.forEach(s => {
      s.dispatch(event);
    });
  };
}
